*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.products{
    padding: 30px 40px;
    width: 100%;
}

.products h3{

    font-size: 42px;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #010f1c;

}
.products p{
    margin-top: 10px;
    color: #9a9a9a;
}
.products .container {
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.filters{
    padding: 20px 30px;
}
.products .container .filters .categories h4 {
    color: #010f1c;
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
}
.products .container .filters .categories ul{

    margin-top: 10px;
}
.products .container .filters .categories ul li{
    list-style: none;
    color: #010f1c;
    margin-top: 20px;
    cursor: pointer;
}
.products .container .product-box{
    margin-right: 0%;
}
.products .container .product-box .contant
{
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
}
.products .container .product-box .contant .box{
    padding: 10px 20px;
    overflow: hidden;
    width: 250px;
    height: 300px;
    border: 1px solid #cbc9cb;
    margin-bottom: 20px;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 5px;
    margin-right: 20px;
}
.products .container .product-box .contant .box:hover{
    box-shadow: rgba(0, 0, 0, 24) 0px 3px 8px;

}


.products .container .product-box .contant .box .imgbox{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e2e0e0;
}
.products .container .product-box .contant .box .imgbox img{
    width: 150px;
    height: 200px;
    margin-right: 30px;
}
.products .container .product-box .contant .box .imgbox .icon{
    z-index: 1;
    position: relative;
    right: -60px;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    transition: 0.5s;
}
.products .container .product-box .contant .box:hover .imgbox .icon{
    transform: translateX(-80px);
}
.products .container .product-box .contant .box .imgbox .icon li{
    list-style: none;
    padding: 10px 10px;
    color: #010f01;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 24) 0px 3px 8px ;
    background: #fff;
    transition: 0.5s;
    font-size: 15px;
}

.products .container .product-box .contant .box .imgbox .icon li:hover{

    color: #fff;
    background: #0989ff;
}
.products .container .product-box .contant .box  .detail{
    margin-top: 20px;
}
.products .container .product-box .contant .box .detail  h5{
    font-size: 16px;
    margin-top: 5px;
    color: #010f01;
    transition: 0.5s;
}
.products .container .product-box .contant .box:hover .detail  h5
{
    color: #0989ff;
}
.products .container .product-box .contant .box .detail  h4{
    color: #0989ff;
}
.product{
    margin-top: 30px;
    padding: 40px 30px;
    width: 100%;
    height: 120%;
    position: fixed;
    z-index: 1;
    margin-top: -30%;
    margin-bottom: 30%;
    background: rgba(0, 0, 0, 0, 24);
 

}
.product .containr{
    margin-top: 7%;
    margin-left: 9%;
    width: 800px;
    height: 550px;
    background: #fff;
    padding: 40px 40px;
    border-radius: 12px;
    box-shadow: rgba(0, 0, 0, 0, 24) 0px 3px 8px;
    
}
.product .containr button{

    font-size: 20px;
    height: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    background: none;
    margin-left: 100%;
    
    }
    .product .containr .product-box{
        padding: 20px 30px ;
        max-width: 100%;
        margin-top: 20px;
        display: flex;

    }
    .product .containr .product-box .img-box{
        padding: 20px 30px;
        width: 300px;
        height: 400px;
        background: #f1f1f1;
    }
    .product .containr .product-box .img-box img{
        width: 200px;
        height: 250px;
    }
    .product .containr .product-box  .detail{
        margin-left: 60px;
        margin-top: 10px;
    }
    .product .containr .product-box  .detail h4{
        font-size: 16px;
        text-transform: uppercase;
        font-weight: 200;
        color: #9a9a9a;
        
    }
    .product .containr .product-box  .detail h3{
        margin-top: 25px;
        font-size: 30px;
        text-transform: capitalize;
        color: #010f01;
        letter-spacing: 1px;
    }
    .product .containr .product-box  .detail p{
        margin-top: 10px;
        color: #4a4a4a;
    }
    .product .containr .product-box  .detail h2{
        color: #010f01;
        font-size: 30px;
        margin-top: 10px;
        letter-spacing: 1px;
    }
    .product .containr .product-box  .detail button{
        border: none;
        outline: none;
        color: #fff;
        background: #010f01;
        font-size: 15px;
        letter-spacing: 1px;
        margin-top: 25px;
        text-align: center;
        cursor: pointer;
        
        padding: 20px 60px;
        
        margin-left: 40px;
        justify-content: center;
    }

    .product .containr .product-box  .detail button:hover{
        background: #0989ff;
    }