*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.footer{
    padding: 30px 40px;
    width: 100%;
    background: #ecf2f7;
}
.footer .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.footer .container .about {
   max-width: 30%;}

.footer .container .about .detail  p{
    font-size: 16px;
    margin-top: 20px;
    color: #828282;
}
.footer .container .about .detail .icon {
    margin-top: 20px;
    display: flex;
    font-size: 25px;

}
.footer .container .about .detail .icon li{
    margin-left: 20px;
    padding: 10px 10px;
    box-shadow: rgba(0, 0, 0, 15) 1.95px, 1.95px, 2.6px ;
    cursor: pointer;
    transition: 0.5s;
    text-decoration: none;
    list-style: none;
}
.footer .container .about .detail .icon li:hover{
    background: #0989ff;
    color: black;
    
    border-radius: 5px;
}
.footer .container .account{
    margin-right: 100px;
    margin-top: 30px;
}

.footer .container .account h3{
    font-size: 35px;
    color: #010f1c;
}
.footer .container .account ul{
    margin-top: 10px;
    margin-left: 20px;
    color: #010f1c;
    font-size: 30px;
    transition: 0.5s;
    cursor: pointer;
}
.footer .container .account ul li:hover{
    color: #0989ff;
}

footer .container .page{
    margin-right: 70px;
    top: 40px;
}

.footer .container .page h3{
    margin-top: 30px;
    font-size: 35px;
    color: #010f1c;
}
.footer .container .page ul{
    margin-top: 10px;
    margin-left: 20px;
    color: #010f1c;
    font-size: 30px;
    transition: 0.5s;
    cursor: pointer;
}
.footer .container .page ul li:hover{
    color: #0989ff;
}