*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
.top-banner{
    padding: 30px 40px;
    width: 100%;
    background: #e3edf6;
}
.top-banner .container{
    max-width: 100%;
    justify-content: space-between;
    display: flex;
}
.top-banner .container .detail{
    margin-top: 10%;
}
.top-banner .container .detail h2{
    font-size: 50px;
    color: #010f1c;
    margin-right: 40%;
    margin-bottom: 30px;
}
.top-banner .container .detail .link {
    margin-top: 22px;
    padding: 10px 10px ;
    text-decoration: none;
    color: #010f1c;
    background-color: aliceblue;
    border-radius: 5px;
    transition: 0.5s;
}
.top-banner .container .detail .link:hover{
    background: #010f1c;
    color: #e3edf6;
}
.producttype{
    width: 100%;
    padding: 20px 20px;
}
.producttype .container{
    width: 100%;
    justify-content: space-between;
    display: flex;
}
.producttype .container .box{
    padding: 10px 20px;
    margin-left: 10px;
}

.producttype .container .box .img-box{
    height: 200px;
    width: 200px;
    border-radius: 50%;
    overflow: hidden;
    background: #e3edf6;
    cursor: pointer;
}

.producttype .container .box .img-box img{
    height: 70px;
    width: 50px;
    justify-content: center;
    align-items: center;
    width: 80%;
    margin-top: 60px;
    margin-left: 20px;
    transition: 0.5s;


}
.producttype .container .box .img-box:hover img{
    transform: scale(1.2);
}
.producttype .container .box .detail{
    max-width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.producttype .container .box .detail p
{
    margin-top: 10px;
    color: #9a9a9a;
}
.about{
    padding: 40px 50px;
    width: 100%;
}
.about .container
{
    width: 100%;
    display: flex;
    justify-content: space-between;
    
}
.about .container .box
{
    display: flex;
    padding: 10px 20px;
    background: #ecf2f7;
    width: 270px;
    cursor: pointer;
    border-radius: 10px;
    height: 90px;
}
.about .container .box .icon 
{
    color: #e44c8c;
    font-size: 33px;
    margin-top: 10px;
}
.about .container .box .detail
{
    margin-top: 10px;
    margin-left: 10px;
}
.about .container .box .detail h3{
    color: #010f1c;
}
.about .container .box .detail p 
{
    color: #9a9a9a;
}
.product{
    padding: 30px 40px;
    width: 100%;
}
.product .container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.product h1 {
    font-size: 32px;
    color: #010f1c;
    margin-bottom: 20px;
}
.product .container .box{
    padding: 10px 20px;
    overflow: hidden;
    width: 250px;
    height: 300px;
    border: 1px solid #cbc9cb;
    margin-bottom: 20px;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 5px;
}
.product .container .box{
    box-shadow: rgba(0, 0, 0, 24) 0px 3px 8px;
}
.product .container .box .imgbox{
    display: flex;
    justify-content: space-between;
}

.product .container .box .imgbox img{
    width: 150px;
    height: 200px;
    margin-right: 30px;
}
.product .container .box .imgbox .icon{
    z-index: 1;
    position: relative;
    right: -60px;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    transition: 0.5s;
}
.product .container .box .imgbox:hover .icon {
    transform: translateX(-80px);
}
.product .container .box .detail{
    margin-top: 10px;
}
.product .container .box .detail p{
    color: #9a9a9a;
    font-size: 15px;
}

.product .container .box .detail h3{

    font-size: 20px;
    margin-top: 5px;
    color: #010f01;
    transition: 0.5s;
}

.product .container .box:hover .detail h3{
    color: #0989ff;
}






.product .container .box .imgbox .icon li{
    list-style: none;
    padding: 10px 10px;
    color: #010f01;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 24) 0px 3px 8px ;
    background: #fff;
    transition: 0.5s;
    font-size: 15px;
}

.product .container .box .imgbox .icon li:hover{
    color: #fff;
    background: #0989ff;
}
.banner{
    padding: 20px 30px;
    width: 100%;

}
.banner .container
{
    padding: 30px 40px;
    
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
}
.banner .container .detail{
    margin-top: 10%;
    margin-left: 20px;
}
.banner .container .detail h4{
    
    font-size: 13px;
    letter-spacing: 1px;
}
.banner .container .detail h3{
    margin-top: 10px;
    font-size: 32px;
    letter-spacing: 1px;
}
.banner .container .detail p{
    font-size: 30px;
    font-weight: 300;
    margin-bottom: 20px;
}
.banner .container .detail .link{
    position: relative;
    margin-bottom: -25px;
    padding: 20px 30px;
    color: #cbc9cb;
    background: #010f01;
    text-decoration: none;
    border-radius: 5px;
    transition: 0.5s;
}
.banner .container .detail .link:hover{
    color: #010f1c;
    background: #0989ff;
}
.product{
    margin-top: 30px;
    padding: 40px 30px;
    width: 100%;
    height: 120%;
    position: fixed;
    z-index: 1;
    margin-top: -30%;
    margin-bottom: 30%;
    background: rgba(0, 0, 0, 0, 24);
 

}
.product .containr{
    margin-top: 7%;
    margin-left: 9%;
    width: 800px;
    height: 550px;
    background: #fff;
    padding: 40px 40px;
    border-radius: 12px;
    box-shadow: rgba(0, 0, 0, 0, 24) 0px 3px 8px;
    
}
.product .containr button{

    font-size: 20px;
    height: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    background: none;
    margin-left: 100%;
    
    }
    .product .containr .product-box{
        padding: 20px 30px ;
        max-width: 100%;
        margin-top: 20px;
        display: flex;

    }
    .product .containr .product-box .img-box{
        padding: 20px 30px;
        width: 300px;
        height: 400px;
        background: #f1f1f1;
    }
    .product .containr .product-box .img-box img{
        width: 200px;
        height: 250px;
    }
    .product .containr .product-box  .detail{
        margin-left: 60px;
        margin-top: 10px;
    }
    .product .containr .product-box  .detail h4{
        font-size: 16px;
        text-transform: uppercase;
        font-weight: 200;
        color: #9a9a9a;
        
    }
    .product .containr .product-box  .detail h3{
        margin-top: 25px;
        font-size: 30px;
        text-transform: capitalize;
        color: #010f01;
        letter-spacing: 1px;
    }
    .product .containr .product-box  .detail p{
        margin-top: 10px;
        color: #4a4a4a;
    }
    .product .containr .product-box  .detail h2{
        color: #010f01;
        font-size: 30px;
        margin-top: 10px;
        letter-spacing: 1px;
    }
    .product .containr .product-box  .detail button{
        border: none;
        outline: none;
        color: #fff;
        background: #010f01;
        font-size: 15px;
        letter-spacing: 1px;
        margin-top: 25px;
        text-align: center;
        cursor: pointer;
        
        padding: 20px 60px;
        
        margin-left: 40px;
        justify-content: center;
    }

    .product .containr .product-box  .detail button:hover{
        background: #0989ff;
    }