*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}
.free{
    padding: 30px 20px;
    background-color: black;
    width: 100%;
    display: flex;
}
.free .icon{
    color: burlywood;
    font-size: 25px;
}
.free p{
    margin-top: 8px;
    color: azure;
    margin-left: 10px;

}
.mainheader{
    padding: 30px 40px;
    width: 100%;
    border-bottom: solid 1px#5555 ;
}
.mainheader .container {
        
    display: flex;
    justify-content: space-between;

}
.mainheader .container .searchbox{
    margin-right: 30px;
}

.mainheader .container .searchbox input{
    outline: none;
    padding: 10px 30px;
    width: 300px;
    border: 3px solid #0989ff;
    color: black;
}

.mainheader .container .searchbox button{
    outline: 3px solid #0989ff;
    border: none;
    cursor: pointer;
    padding: 10px 20px;
    background: #0989ff;
    color: antiquewhite;
}
.mainheader .container .icon{
    display: flex;
    margin-right: 50px;
}
.mainheader .container .icon .account{
    display: flex;
    margin-right: 30px;
}


.mainheader .container .icon .account .user-icon {
    margin-right: 10px;
    font-size: 20px;
    cursor: pointer;
    color: black;

}

.mainheader .container .icon .account p {
    margin-right: 5px;
    margin-top: 2px;
}

.mainheader .container .icon .second-icon {
    display: flex;
}
.mainheader .container .icon .second-icon .link {
    margin-right: 5px;
    font-size: 22px;
    cursor: pointer;
    color: #0989ff;
}
.header {
    width: 100%;
    padding: 30px 20px;
    box-shadow: rgba(0, 0, 0, 35) 0px 5px 30px;
}
.header .container{
    justify-content: space-between;
    max-width: 100%;
    display: flex;
}
.header .container .nav ul{
    display: flex;
}
.header .container .nav ul  li{
    margin-left: 20px;
    list-style: none;
}
.header .container .nav ul  li .link{
    text-decoration: none;
    color: black;
    transition: 0.5s;
}
.header .container .nav ul  li .link:hover{
    color: #0989ff;
}
.header .container .auth {
    margin-right: 20px;
    font-size: 22px;
    color: #0989ff;
    cursor: pointer;
}
.header .container .auth button{
    margin-right: 20px;
    font-size: 22px;
    color: #0989ff;
    cursor: pointer;
    border: none;
    outline: none;
    background: none;
}



