*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.cartcontainer{
    padding: 40px 50px;
    width: 100%;
    background: #f1f1f1;
    border-bottom: 1px solid #9a9a9a;
}
.cartcontainer .emptycart{
    margin-top: 30px;
    margin-left: 40%;
}
.cartcontainer .emptycart h2{
    font-size: 32px;
    color: #010f1c;
    text-transform: uppercase;
    font-weight: 300;
    margin-bottom: 30px;
}
.cartcontainer .emptycart .emptycartbtn {
    margin-left: 70px;
    text-decoration: none;
    color: #fff;
    padding: 10px 20px;
    background: #010f1c;
}

.cartcontainer .contant{
    max-width: 100%;
    padding: 20px 30px;
}
.cartcontainer .contant .cartitem {
    display: flex;
    padding: 20px 30px;
    background: #a7a2a2;
    border-radius: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
}
.cartcontainer .contant .cartitem .imgbox{
    padding: 20px 20px;
    background: #f1f1f1;
}
.cartcontainer .contant .cartitem .imgbox img{
    width: 250px;
    height: 250px;
}
.cartcontainer .contant .cartitem  .detail{
    margin-left: 60px;
    display: flex;
    margin-top: 30px;
    padding: 20px 30px;
}
.cartcontainer .contant .cartitem  .detail .info h4{
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 120;
    color: #1a1919;
    letter-spacing: 1px;
}
.cartcontainer .contant .cartitem  .detail .info h3{
    margin-top: 20px;
    color: #010f1c;
    letter-spacing: 1px;
    font-size: 20px;
    font-weight: 200;
}
.cartcontainer .contant .cartitem  .detail .info p{
    margin-top: 10px;
    color: #1a1919;
    letter-spacing: 1px;

}
.cartcontainer .contant .cartitem  .detail .info .qty{
    margin-top: 20px;
    display: flex;

}
.cartcontainer .contant .cartitem  .detail .info .incqty{
    padding: 10px 10px;
    background: none;
    outline: none;
    border: none;
    font-size: 30px;
    color: #0989ff;
}
.cartcontainer .contant .cartitem  .detail .info .dcqty{
    padding: 10px 10px;
    background: none;
    outline: none;
    border: none;
    font-size: 30px;
    color: #0989ff;
}
.cartcontainer .contant .cartitem  .detail .info .qty input{
    border: none;
    outline: none;
    background: none;
    font-size: 22px;
    color: #1a1919;
    width: 50px;
    margin-left: 20px;
   
}
.cartcontainer .contant .cartitem  .detail .info .subtotal{
    color: #1a1919;
    font-size: 20px;
    margin-top: 10px;
}
.cartcontainer .contant .cartitem  .detail .close{
    margin-top: 30px;
    margin-left: 40px;
}
.cartcontainer .contant .cartitem  .detail .close button{
    border: none;
    outline: none;
    background: none;
    color: #db0045;
    font-size: 22px;
    cursor: pointer;
}
.cartcontainer .Totalprice{
    margin-top: 30px;
    margin-left: 50%;
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: 1px;
   
}
.cartcontainer .checkout{
    margin-top: 40px;
    margin-left: 50%;
    padding: 10px 20px;
    color: #fff;
    background: #0989ff;
    border: none;
    outline: none;
    cursor: pointer;

}